<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");
    let blob5 = $("#blob-5");
    let blob6 = $("#blob-6");
    let aboutBox = $("#aboutBox").getBoundingClientRect();
    let imgmontevideo = $("#imgmontevideo").getBoundingClientRect();

    const xmultiplier = window.innerWidth / 960;
    const overlapPixels = xmultiplier * 10;
    const initialOffset = window.pageYOffset;

    const imgmontevideoYOffset = imgmontevideo.top + initialOffset;
    
    
    const aboutBoxYOffset = aboutBox.top + initialOffset;
    const aboutBoxXOffset = aboutBox.left + overlapPixels;

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 3500;
        let xmultiplier = window.innerWidth / 960;
        // big blue top corner
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            ((960 * xmultiplier).toFixed() - blob1.width).toFixed() +
            "px," +
            pos(0, -200, relativeY, 0) +
            "px, 0)"
        );
        // big red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            500 * xmultiplier +
            "px," +
            pos(454, -240, relativeY, 0) +
            "px, 0)"
        );

        // small red&blue
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            248 * xmultiplier +
            "px," +
            pos(464, -2500, relativeY, 0) +
            "px, 0)"
        );

        // workspace office
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            (aboutBoxXOffset - blob4.getBoundingClientRect().width).toFixed() +
            "px," +
            pos(aboutBoxYOffset, -640, relativeY, 0) +
            "px, 0)"
        );

        // 2nd big red
        prefix(
          blob5.style,
          "Transform",
          "translate3d(" +
            440 * xmultiplier +
            "px," +
            pos(894, -240, relativeY, 0) +
            "px, 0)"
        );

        prefix(
          blob6.style,
          "Transform",
          "translate3d(" +
            -50 * xmultiplier +
            "px," +
            pos(imgmontevideoYOffset + 300, -340, relativeY, 0) +
            "px, 0)"
        );

        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
        blob5.classList.add("force-show");
        blob6.classList.add("force-show");
      },
      startParallax
    );
  }
  onMount(async () => {
    startParallax();
  });
  import Nav from "../components/Nav.svelte";
  import Footer from "../components/Footer.svelte";
</script>

